body {
  background-color: #FFFFFF;
  padding: 20px;
  margin: 0;
  font-family: 'Fira Sans Extra Condensed', sans-serif;

}

h1, h2, p, ul, li {
  /* font-family: sans-serif; */
}
ul.header li {
  display: inline ;
  list-style-type: none;
  margin: 10px;

}
ul.header {
  background-color: #ff851b;
  padding: 3px;
}

ul.logo {
  background-color: ##fffff;
  padding: 0;
}

ul.header li a {
  display: inline-block;
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 8px;
}
.content {
  background-color: #FFFFFF;
  padding: 10px;
  margin-top: 60px;
}
