.active {
  background-color: #000AB0;
  color: white;
}


a {
  text-decoration: none;
  color: white;
  font-weight:100;

  margin: 0px;
  padding: 10px;
}

.containerA {  
  
  display: grid;
  margin-top: 50px;
  background-color: #ff851b; 
  grid-template-rows: 45px;
  grid-template-columns: 250px 10% auto 15% 15% 15% ;
  grid-template-areas:
    "A B C D E F";
  }

.A { grid-area: A; max-height: 240px; justify-self: start; align-self: center; }

.B { grid-area: B;justify-self: end; align-self: center}

.C { grid-area: C; justify-self: end; align-self: center}

.D { grid-area: D; justify-self: end; align-self: center} 

.E { grid-area: E;justify-self: end; align-self: center } 

.F { grid-area: F; justify-self: end; align-self: center}
